<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'
  import { transitionTime } from '../helpers/transitionTime'
  import { focusContenteditable } from '../helpers/focusContenteditable'
  import { cards } from '../cards'
  import { params, navigate } from '../components/Router.svelte'
  import ScrollView from '../components/ScrollView.svelte'
  import Header from '../components/Header.svelte'
  import Button from '../components/Button.svelte'

  $: card = $cards.find((c) => c.id === parseInt($params.get('id')))
  $: cardIsExist = Boolean(card)

  // If card is not exist
  onMount(() => {
    if (!cardIsExist) {
      cards.add('md')
      navigate('/editor?id=' + $cards[0].id, true)
    }
  })

  function editor(node) {
    node.innerHTML = card.content
    // Saving card
    node.oninput = () => {
      if (node.innerText.slice(-1) === '\n') {
        $cards[$cards.indexOf(card)].content = node.innerText.slice(0, -1)
      } else {
        $cards[$cards.indexOf(card)].content = node.innerText
      }
    }
    setTimeout(() => focusContenteditable(node), transitionTime)
  }
</script>

<style>
  .editor {
    display: block;
    box-sizing: border-box;
    padding: 1.5rem 0;
    color: inherit;
    background: var(--background);
    font-size: 1rem;
    font-family: inherit;
    line-height: var(--line-height);
    white-space: pre-wrap;
  }
</style>

<ScrollView>
  <Header>
    <Button
      icon="navigate_before"
      class="transparent"
      on:click={() => window.history.back()} />
    <Button
      icon="delete"
      class="transparent"
      on:click={() => {
        cards.remove(card.id)
        window.history.back()
      }} />
  </Header>

  <div class="container">
    {#if cardIsExist}
      <div
        class="editor"
        placeholder="Type here..."
        contenteditable
        out:fade={{ duration: transitionTime / 2 }}
        use:editor />
    {/if}
  </div>
</ScrollView>
